<template>
    <div class="users">
        
        
        <div slot="header" class="clearfix" style="height:60px;line-height:60px">
            <div>
                <div style="font-size:20px;font-weight:bold;height:50px;line-height:50px;width:48%;float:left;">负责人管理</div>
                <div style="float:right;margin-right:10px;height:50px;line-height:50px;width:50%;text-align:right">
                    <el-input v-model="userInput" placeholder="请输入负责人名字" style="width:200px"></el-input><el-button type="info" @click="cheack">查询</el-button>
                    <el-button type="primary" @click="addUser">添加</el-button>
                </div>
          </div>
        </div>
        <el-table
        :data="tableData"
        border
    style="width: 100%;">
    <el-table-column
      prop="cName"
      label="名字"
      width="180"
      >
    </el-table-column>
    <el-table-column
      prop="email"
      label="邮箱地址">
    </el-table-column>
    <el-table-column
      prop="phone"
      label="移动电话">
    </el-table-column>
    <el-table-column
      prop="account"
      label="账号">
    </el-table-column>

    <el-table-column
      prop="methods"
      label="操作"
      >
      <template slot-scope="scope" >

          <el-button
          size="small"
          @click="handleEdit(scope.$index, scope.row)">修改</el-button>

        <el-button
          size="small  "
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>

    </el-table-column>

    </el-table>
    <!-- 修改信息会话框 -->
    <el-dialog title="修改信息" :visible.sync="dialogFormVisible" width="30%">
    <el-form :model="form" label-width="80px">
        <!-- <el-form-item label="修改项目" :label-width="formLabelWidth">
        <el-select v-model="form.region" placeholder="请选择修改项目">
            <el-option  v-for="(item,index) in keys" :key="index" :value="item" :label="item"></el-option>
        </el-select>
        </el-form-item>

        <el-form-item label="修改值" :label-width="formLabelWidth">
        <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item> -->
            <el-form-item label="名字">
                <el-input v-model="form.cName"></el-input>
            </el-form-item>
            <el-form-item label="账号">
                <el-input v-model="form.account"></el-input>
            </el-form-item>
            <el-form-item label="密码">
                <el-input v-model="form.password" show-password></el-input>
            </el-form-item>
            <el-form-item label="邮箱">
                <el-input v-model="form.email"></el-input>
            </el-form-item>
            <el-form-item label="电话">
                <el-input v-model="form.phone"></el-input>
            </el-form-item>

    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="updateUser">确 定</el-button>
    </div>
</el-dialog>

<!-- 增加信息会话框 -->
    <el-dialog
        title="增加配置信息"
        :visible.sync="dialogVisible"
        width="30%"
    >

        <el-form ref="form" :model="newform" label-width="80px">
            <el-form-item label="名字">
                <el-input v-model="newform.cName"></el-input>
            </el-form-item>
            <el-form-item label="账号">
                <el-input v-model="newform.account"></el-input>
            </el-form-item>
            <el-form-item label="密码">
                <el-input v-model="newform.password" show-password></el-input>
            </el-form-item>
            <el-form-item label="邮箱">
                <el-input v-model="newform.email"></el-input>
            </el-form-item>
            <el-form-item label="电话">
                <el-input v-model="newform.phone"></el-input>
            </el-form-item>

        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="addMessage">确 定</el-button>
        </span>
    </el-dialog>
</div>
</template>

<script>
export default {
    name:'Users',
    data(){
        return{
            //表格数据
            tableData:[],
            //添加负责人信息
            newform:{
                cName:'',
                account:'',
                password:'',
                email:'',
                phone:''
            },
            //修改会话框的显示控制
            dialogFormVisible:false,
            formLabelWidth:'120px',
            //修改提交数据
            form: {
                name: '',
                region: ''
            },
            //属性值数组
            keys:[],
            //修改信息的位置
            index:'',
            //添加负责人会话框控制
            dialogVisible:false,
            //当前负责人信息
            personMessege:{},
            //关键字
            userInput:''
        }
    },
    methods:{
        //修改
        handleEdit(index, row) {
            this.index = index
            this.dialogFormVisible = true
            this.keys = Object.keys(row)
            this.personMessege = row
            this.form = row

        },
        //删除
        handleDelete(index, row) {
            this.$confirm('此操作将永久删除, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$axios.delete('http://192.168.1.155:8888/CPController/'+row.cId).then((msg)=>{
                        if (msg.data) {
                            this.$message({
                                message: '恭喜你，删除成功',
                                type: 'success'
                            });
                            window.location.reload()
                        }else{
                            this.$message.error('删除失败');
                        }
                    })

                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });          
            });

        },
        //修改提交信息
        updateUser(){
            this.dialogFormVisible = false
            let obj = this.personMessege
            obj[this.form.region] = this.form.name
            this.$axios.put('http://192.168.1.155:8888/CPController',obj).then((msg)=>{
                if (msg.data) {
                    this.$message({
                        message: '恭喜你，修改成功',
                        type: 'success'
                    });
                    window.location.reload()
                }else{
                    this.$message.error('修改失败');
                }
            })
        },
        //显示添加负责人会话框
        addUser(){
            this.dialogVisible = true
        },
        //添加负责人
        addMessage(){
            this.dialogVisible = false
            this.$axios.post('http://192.168.1.155:8888/CPController/insert',this.newform).then((msg)=>{
                if (msg.data) {
                    this.$message({
                        message: '恭喜你，添加成功',
                        type: 'success'
                    });
                    window.location.reload()
                }else{
                    this.$message.error('添加失败');
                }
            })
        },
        //查询负责人
        cheack(){
            let obj = {
                'cName':this.userInput
            }
            this.$axios.post('http://192.168.1.155:8888/CPController/query',obj).then((msg)=>{
                if (!msg.data.length) {
                //没有数据
                this.$message({
                    message: '没有查找到数据',
                    type: 'warning'
                });
                }else{
                this.tableData = msg.data
                }
                this.userInput = ''
            })
        },
    },
    mounted(){
        this.$axios.get('http://192.168.1.155:8888/CPController').then((msg)=>{
            this.tableData = msg.data
        })
    }
}
</script>

<style scoped>

</style>>



